import Json from '@/Json';
import uniLoadMore  from '@/components/uni-load-more/uni-load-more.vue';

var _self,
page = 1,
size = 8;

export default {
	components:{
		uniLoadMore
	},
	data() {
		return {
			navList: [{
					state: 0,
					text: '全部',
					loadingType: 'more',
					orderList: []
				},
				{
					state: 1,
					text: '待付款',
					loadingType: 'more',
					orderList: []
				},
				{
					state: 2,
					text: '待服务',
					loadingType: 'more',
					orderList: []
				},
				{
					state: 3,
					text: '待评价',
					loadingType: 'more',
					orderList: []
				},
				{
					state: 4,
					text: '售后',
					loadingType: 'more',
					orderList: []
				}
			],
			tabCurrentIndex:0,
			contentText: {
				contentdown: "上拉显示更多哦",
				contentrefresh: "正在加载...",
				contentnomore: "没有更多数据了"
			},
			moreLoadingType: 0
		}
	},
	onLoad(options) {
		/**
		 * 修复app端点击除全部订单外的按钮进入时不加载数据的问题
		 * 
		 */
		_self = this;
		// console.log("options",options);
		this.tabCurrentIndex = +options.state;




		if(options.state == 0){
			this.loadData()
		}

	},
	onPullDownRefresh() {
		this.loadData();
	},
	// onReachBottom(){
	// 	console.log("bottom")
	// 	this.loadData()
	// 	uni.showNavigationBarLoading()
	// },
	methods: {
		// 获取订单列表
		loadData(source){
			//这里是将订单挂载到tab列表下
			let index = this.tabCurrentIndex;
			let navItem = this.navList[index];
			let state = navItem.state;
			
			if(source === 'tabChange' && navItem.loaded === true){
				//tab切换只有第一次需要加载数据
				return;
			}
			if(navItem.loadingType === 'loading'){
				//防止重复加载
				return;
			}
			
			navItem.loadingType = 'loading';
			
			setTimeout(()=>{
				let orderList = Json.orderList.filter(item=>{
					//添加不同状态下订单的表现形式
					item = Object.assign(item, this.orderStateExp(item.state));
					//演示数据所以自己进行状态筛选
					if(state === 0){
						//0为全部订单
						return item;
					}
					return item.state === state
				});
				console.log(orderList)
				orderList.forEach(item=>{
					navItem.orderList.push(item);
				})
				//loaded新字段用于表示数据加载完毕，如果为空可以显示空白页
				this.$set(navItem, 'loaded', true);
				
				//判断是否还有数据， 有改为 more， 没有改为noMore 
				navItem.loadingType = 'more';
				
			}, 600);	

		},
		loadMore(){
			//这里是将订单挂载到tab列表下
			let index = this.tabCurrentIndex;
			let navItem = this.navList[index];
			let state = navItem.state;
			
			if(_self.moreLoadingType !== 0 ){
				return false;
			}
			if(navItem.loadingType === 'loading'){
				//防止重复加载
				return;
			}
			
			navItem.loadingType = 'loading';
			
			_self.moreLoadingType = 1;
			// uni.showNavigationBarLoading();
			// console.log("生命周期中的this",this);
			
			let orderList = [];
			setTimeout( () => {
				
				uni.request({
					url: "http://mengxuegu.com:7300/mock/5e7dba791c5f7f209577b51d/home/order/list/"+page+"/"+size,
					method: "post",
					success(res) {
						console.log(res)
						// console.log(this)
						let resList = res.data.data;
						orderList = resList.filter( item => {
							item = Object.assign(item, _self.orderStateExp(item.state));
							if(state === 0){
								return item;
							}
							return item.state === state;
						})
						// console.log(navItem.orderList);
						navItem.orderList = navItem.orderList.concat(orderList); // 使用concat方式
						// orderList.forEach( item => {
						// 	navItem.orderList.push(item)
						// })
						// console.log(navItem.orderList);
						 _self.$set( navItem,'loaded',true); // 新字段需要使用￥set
						// 判断是否还有数据， 有改为 more， 没有改为noMore 
						navItem.loadingType = 'more';
						_self.moreLoadingType = 0;
					}
				})
			},500)	
		},
		clickTab(index){
			this.tabCurrentIndex = index;
			console.log(this.tabCurrentIndex)
		},
		changeTab(e){ // current 改变时会触发 change 事件
			console.log(e.target.current);
			this.tabCurrentIndex = e.target.current; 
			this.loadData('tabChange');
		},
		orderStateExp(state){
			let stateTip = '',
				stateTipColor = '#fa436a';
			switch(+state){
				case 1:
					stateTip = '待付款';
					break;
				case 2:
					stateTip = '待服务';
					break;
				case 3:
					stateTip = '待评价';
					break;
				case 4:
					stateTip = '售后';
					break;
				case 5:
					stateTip = '订单关闭';
					break;	
			}
			return {stateTip, stateTipColor};
		},
	}
}
